module.exports = {

  ESCAPE_KEY: 27,

  stylePrefix: 'com-',

  defaults: [],

  // Editor model
  em: null,

  // If true center new first-level components
  firstCentered: true,

  // If true the new component will created with 'height', else 'min-height'
  newFixedH: false,

  // Minimum height (in px) of new component
  minComponentH: 50,

  // Minimum width (in px) of component on creation
  minComponentW: 50,
};



